.loading {
    background-color: rgba(0,0, 0, 0.5);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}

.status-wrapper {
    position: relative;
    width: 97%;
    margin: 2px auto 2px auto;
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.status-wrapper::after {
    content: "";
    margin: 5px;
}

.status-wrapper .header {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    overflow: auto;
}

.status-wrapper .header .left-section {
    flex-direction: row;
    display: flex;
}

.status-wrapper .header .left-section .avatar {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    -webkit-margin-end: 5px;
            margin-inline-end: 5px;
    display: flex;
    justify-content: center;
}

.status-wrapper .header .left-section .user-info-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.status-wrapper .header .left-section .user-info-section .user-name {
    font-weight: 700;
    text-transform: capitalize;
}

.status-wrapper .header .left-section .user-info-section .user-email {
    font-weight: 200;
    font-style: italic;
}

.status-wrapper .right-section {
    -webkit-margin-end: 10px;
            margin-inline-end: 10px;
}

.status-wrapper .right-section .action-button {
    border: none;
    background-color: inherit;
    cursor: pointer;
    outline: none;
    font-size: 1.5em;
    opacity: 1;
}

.status-wrapper .right-section .action-button:active {
    opacity: 0.7;
}

.status-wrapper .main-section {
    text-align: center;
    font-weight: 500;
}
.message-writter-wrapper {
    width: 99%;
    margin:7px auto 7px auto;
    height: 50px;
    border: 1px solid rgba(0, 0, 0, 0.33);
    display: flex;
    justify-content: center;
    border-radius: 7px;
    overflow: hidden;
}

.message-writter {
    width: 100%;
    border: none;
    text-indent: 10px;
}

.message-writter-save, .message-writter-save:disabled {
    color: white;
    padding: 14px 20px;
    border: none;
    background-color: #4CAF50;
    outline: none;
}

.message-writter-save:disabled {
    cursor: no-drop;
    opacity: 0.7;
}

.message-writter-save{
    cursor: pointer;
}

.message-writter-save:active {
    opacity: 0.9;
}
.app-loading-indicator {
    width: 50px;
    height: 50px;
    border-radius: 100%;
    border-top: 5px solid purple;
    border-left: 5px solid purple;
    border-right: 5px solid purple;
    border-bottom: 5px solid white;
    /* animation: loading 1s linear 0 infinite; */
    -webkit-animation-name: loading;
            animation-name: loading;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@-webkit-keyframes loading {
    0%   {transform: rotateZ(0deg);}
    25%  {transform: rotateZ(90deg);}
    50%  {transform: rotateZ(180deg);}
    75%  {transform: rotateZ(270deg);}
    100% {transform: rotateZ(360deg);}
}

@keyframes loading {
    0%   {transform: rotateZ(0deg);}
    25%  {transform: rotateZ(90deg);}
    50%  {transform: rotateZ(180deg);}
    75%  {transform: rotateZ(270deg);}
    100% {transform: rotateZ(360deg);}
}

.app-loading-container { 
    width: 100%;
     height: 100%;
     display: flex;
     justify-content: center;
     align-items: center;
}

